@import-normalize; /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: radial-gradient(
    farthest-corner circle at 0% 0%,
    rgba(0, 58, 117, 0.2) 0%,
    rgb(16, 20, 24) 100%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  position: relative;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 20px;
}

.tab-panel {
  height: 90vh;
  /* background: red; */
}

.player {
  position: absolute;
  right: 0;
  top: 0;

  margin-left: auto;
  display: block;
  display: flex;
  color: aliceblue;
  align-items: center;
}
.player p {
  margin-left: 20px;
}

.first-panel {
  display: flex;
  /* align-items: center; */
  /* justify-content: space-evenly; */
  margin-bottom: 100px;
}
.first-panel > div {
  display: block;
  margin: 5px;
  width: calc(100% / 3);
}

@media screen and (max-width: 412px) {
  .container {
    max-width: 420px;
  }
}
